@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&display=swap');

:root {
  --primary-purple: #7E6BF2;
  --dark-blue: #03174C;
  --card-gradient: linear-gradient(45deg, #7E6BF2, #4B3EAE);
}

body {
  font-family: 'Space Grotesk', sans-serif;
}

.meditation-card {
  border-radius: 20px;
  overflow: hidden;
  transition: transform 0.2s ease-in-out;
}

.meditation-card:hover {
  transform: translateY(-4px);
}

.feature-card {
  border-radius: 16px;
  padding: 24px;
  height: 180px;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
}

.feature-card:hover {
  transform: scale(1.02);
}

.nav-button {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
  padding: 8px 16px;
  color: var(--dark-blue);
  transition: color 0.2s ease;
}

.nav-button:hover {
  color: var(--primary-purple);
}

.gradient-text {
  background: var(--card-gradient);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.hero-section {
  background: linear-gradient(180deg, #03174C 0%, #051D3B 100%);
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px;
  color: white;
}
